*{
  box-sizing: border-box;
  margin:0;
  padding:0;
  
}
html{scroll-behavior: smooth;}
html, body{
  font-family: 'Roboto', sans-serif;
  font-size: 1em;
}


h1,
h2,
h3,
h4,
h5,
h6{color: #1d1d1d;}

h1{
  font-size: 4em;
}
h2{
  font-size: 3em;
}
h3{
  font-size: 2.6em;
}
h4{
  font-size: 2em;
}
h5{
  font-size: 1.6em;
}
h6{
  font-size: 1.3em;
}

.pramodize-loading{
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;

  transition: all 1s ease-in-out;
  
}

.wrapper{
  width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (max-width: 1024px){
  .wrapper{
    width: 100%;
  }
}